.theme-setting .theme-setting-toggle {
  transition: all 0.5s;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #f73f52;
  color: #fff;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
