  line-height: 60px;
  cursor: pointer;
}
.theme-setting .theme-setting-toggle i {
  font-size: 24px;
}
.theme-setting .theme-setting-options {
  transition: all 0.5s;
  transition-delay: 0.3s;
  z-index: -1;
  position: absolute;
  left: -220px;
  bottom: 0;
  height: 150px;
  width: 50px;
  background-color: #e8e6e6;
